nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

.brandName {
  font-size: 32px;
  font-weight: 600;
}

ul {
  display: flex;
  gap: 10px;
  list-style-type: none;
}

li {
  border: solid 1px;
  color: black;
  border-top: none;
  border-bottom: none;
  padding: 0 5px;
}

.links {
  text-decoration: none;
  font-size: 24px;
  font-weight: 600;
}
